<template>
  <div>
    <!--  -->
    <el-row class="block">
      <el-col :span="4">
        <el-cascader v-model="value" :options="options" clearable filterable :getCheckedNodes="true" @change="handleChange"> </el-cascader>
      </el-col>
    </el-row>
    <div>
      <el-card class="box-card">
        <el-row :gutter="40">
          <el-col :span="4">
            <!--  -->
            <el-button el-button class="add" type="success" icon="el-icon-plus" @click="goEdit(newsCategoryId)"></el-button>
          </el-col>
          <el-col :span="10">
            <!--  -->
            <el-input clearable placeholder="" v-model="search" class="input-with-select"> </el-input>
          </el-col>
        </el-row>
        <!--  -->
        <el-table
          :data="tableData.filter(data => !search || data.title.toLowerCase().includes(search.toLowerCase()))"
          border
          style="width: 100%"
          :header-cell-style="{ textAlign: 'center' }"
          :cell-style="{ textAlign: 'center' }"
        >
          <el-table-column type="index" label="" width="50">
            <template slot-scope="scope">
              <span>{{ (currentPage - 1) * pageSize + scope.$index + 1 }}</span>
            </template>
          </el-table-column>
          <el-table-column prop="releaseTime" label="" width=""> </el-table-column>
          <el-table-column prop="title" label="" width=""> </el-table-column>
          <el-table-column prop="id" label="id" width=""> </el-table-column>
          <el-table-column label="" width="">
            <template slot-scope="scope">
              <!-- {{ scope.row.id }} -->
              <div>
                <el-button type="success" @click="look(scope.row.id)"></el-button>
                <el-button type="warning" @click="showEditDialog(scope.row.id)"></el-button>
                <el-button type="info" @click="deleteNews(scope.row.id)"></el-button>
              </div>
            </template>
          </el-table-column>
        </el-table>
        <!--  -->
        <div>
          <!--  -->
          <div>
            <el-pagination
              @size-change="handleSizeChange"
              @current-change="handleCurrentChange"
              :current-page="currentPage"
              :page-sizes="[6, 12, 18]"
              :page-size="100"
              layout="total, sizes, prev, pager, next, jumper"
              :total="total"
            >
            </el-pagination>
          </div>
        </div>
      </el-card>
      <!--  -->
      <el-dialog title="" :visible.sync="editDialogVisible" width="50%" :before-close="handleClose">
        <!-- rulesref -->
        <el-form ref="editFormRef" :model="editForm" label-width="80px" status-icon>
          <el-form-item label="id:">
            <el-input v-model="editForm.id" disabled></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-input v-model="editForm.title"></el-input>
          </el-form-item>
          <el-form-item label=":">
            <el-date-picker v-model="editForm.releaseTime" type="date" placeholder="" format="yyyy  MM  dd " value-format="yyyy-MM-dd"></el-date-picker>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="editDialogVisible = false"> </el-button>
          <el-button type="primary" @click="editNew()"> </el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</template>

<script>
import { getNewsList, getnew, update, deleteNew, fuzzy } from '../../api/api'
export default {
  data() {
    return {
      search: '',
      getShow: true, //()
      gettable: true, //
      visible: false,
      editDialogVisible: false, //
      AdDialogVisible: false, //
      Ad: { username: '', password: '' }, //
      editForm: {}, //
      newsCategoryId: 1, //id
      tableData: [], //
      fuzzytableData: [], //
      pageSize: 6, //
      currentPage: 1, //
      total: 0, //
      fuzzyShow: false, //()
      fuzzytable: false, //
      fuzzyForm: { fuzzytitle: '', fuzzytotal: 0, fuzzycurrent: 1, fuzzysize: 6 }, //
      value: [],
      options: [
        {
          value: 1,
          label: '',
          children: [
            {
              value: 42,
              label: ''
            },
            {
              value: 43,
              label: ''
            },
            {
              value: 44,
              label: ''
            },
            {
              value: 45,
              label: ''
            }
          ]
        },
        {
          value: 22,
          label: '',
          children: [
            {
              value: 27,
              label: ''
            },
            {
              value: 28,
              label: ''
            },
            {
              value: 29,
              label: ''
            },
            {
              value: 30,
              label: ''
            },
            {
              value: 31,
              label: ''
            }
          ]
        },
        {
          value: 23,
          label: '',
          children: [
            {
              value: 35,
              label: ''
            },
            {
              value: 36,
              label: ''
            },
            {
              value: 37,
              label: ''
            }
          ]
        },
        {
          value: 25,
          label: '',
          children: [
            {
              value: 38,
              label: ''
            },
            {
              value: 39,
              label: ''
            }
          ]
        },
        {
          value: 27,
          label: '',
          children: [
            {
              value: 40,
              label: ''
            },
            {
              value: 41,
              label: ''
            }
          ]
        }
      ]
    }
  },
  created() {},
  mounted() {},
  methods: {
    //
    handleClose(done) {
      this.$confirm('')
        .then(_ => {
          done()
        })
        .catch(_ => {})
    },
    //
    handleChange(value) {
      // console.log(value);
      // console.log(value[1]);
      this.currentPage = 1
      this.newsCategoryId = value[1] //id
      this.query(this.newsCategoryId)
    },
    query(newsCategoryId) {
      const data = {
        current: this.currentPage,
        newsCategoryId: newsCategoryId,
        size: this.pageSize
      }
      getNewsList(data)
        .then(res => {
          // console.log(res);
          if (res.code !== 200) {
            return this.$message.error('')
          } else {
            this.tableData = res.data.records
            this.total = res.data.total
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    //
    showEditDialog(id) {
      getnew(id).then(res => {
        // console.log(res);
        if (res.code == 200) {
          this.editForm = res.data
        }
      })
      this.editDialogVisible = true
    },
    //
    editNew() {
      const data = {
        id: this.editForm.id,
        releaseTime: this.editForm.releaseTime,
        title: this.editForm.title
      }
      update(data).then(res => {
        console.log('dsadasas', res)
        if (res.code == 200) {
          this.editDialogVisible = false
          this.$message.success('')
          this.query(this.newsCategoryId)
        }
      })
    },
    //
    fuzzyList(title) {
      // console.log(title);
      if (title == '') {
        return this.$message.error('')
      }
      this.fuzzyShow = true //
      this.getShow = false //
      this.gettable = false
      this.fuzzytable = true
      const data = {
        current: this.fuzzyForm.fuzzycurrent,
        size: this.fuzzyForm.fuzzysize,
        title: title
      }
      fuzzy(data)
        .then(res => {
          // console.log(res);
          if (res.code !== 200) {
            return this.$message.error('')
          } else {
            this.fuzzytableData = res.data.records
            this.fuzzyForm.fuzzytotal = res.data.total
          }
        })
        .catch(err => {
          console.log(err)
        })
    },
    //
    qingKong() {
      this.tableData = []
      this.fuzzytableData = []
      this.fuzzyForm.fuzzytotal = 0
      this.fuzzyShow = false
      this.getShow = true
      this.fuzzytable = false
      this.gettable = true
    },
    //id
    async deleteNews(id) {
      // console.log(id);
      const res = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => {
        return err
      })
      //confirm
      //cancel
      console.log(res)
      if (res !== 'confirm') {
        return this.$message.info('~')
      } else {
        console.log(id)
        const data = {
          id: id
        }
        //data 
        deleteNew(data).then(res => {
          console.log('>>>' + res)
          if (res.code == 200) {
            this.$message.success('')
            this.query(this.newsCategoryId)
          } else {
            return this.$message.error('')
          }
        })
      }
    },
    //
    look(id) {
      this.$router.push({ path: 'news', query: { id: id } })
    },
    //
    showAdDialog() {
      this.AdDialogVisible = true
    },
    handleCurrentChange(val) {
      // console.log(`: ${val}`);
      this.currentPage = val
      this.query(this.newsCategoryId)
    },
    handleSizeChange(val) {
      // console.log(` ${val} `);
      this.pageSize = val
      this.query(this.newsCategoryId)
    },
    handleCurrentChangefuzzy(val) {
      this.fuzzyForm.fuzzycurrent = val
      this.fuzzyList(this.fuzzyForm.fuzzytitle) //
    },
    handleSizeChangefuzzy(val) {
      this.fuzzyForm.fuzzysize = val
      this.fuzzyList(this.fuzzyForm.fuzzytitle) //
    },
    //
    goEdit(id) {
      let title = ''
      console.log(this.value)
      if ((this.value.length = 2)) {
        let t = this.value[0]
        let i = this.value[1]
        this.options.forEach(item => {
          if (item.value == t) {
            title += item.label + '/'
            item.children.forEach(e => {
              if (e.value == i) title += e.label
            })
          }
        })
      }
      if (
        (this.newsCategoryId == 27 && this.total == 1) ||
        (this.newsCategoryId == 28 && this.total == 1) ||
        (this.newsCategoryId == 29 && this.total == 1) ||
        (this.newsCategoryId == 30 && this.total == 1) ||
        (this.newsCategoryId == 31 && this.total == 1)
      ) {
        this.$message.error('')
      } else if (
        this.newsCategoryId == 27 ||
        this.newsCategoryId == 28 ||
        this.newsCategoryId == 29 ||
        this.newsCategoryId == 30 ||
        this.newsCategoryId == 31 ||
        this.newsCategoryId == 35 ||
        this.newsCategoryId == 36 ||
        this.newsCategoryId == 37 ||
        this.newsCategoryId == 38 ||
        this.newsCategoryId == 39 ||
        this.newsCategoryId == 40 ||
        this.newsCategoryId == 41 ||
        this.newsCategoryId == 42 ||
        this.newsCategoryId == 43 ||
        this.newsCategoryId == 44
      ) {
        this.$router.push({ path: '/administrator/edit', query: { id: id, title: title } })
        console.log(this.newsCategoryId)
      } else if (this.newsCategoryId == 45) {
        this.$router.push({ path: '/administrator/editlunbo', query: { id: id, title: title } })
        // console.log(this.newsCategoryId);
      } else {
        this.$message.error('')
      }
    }
  }
}
</script>

<style lang="less">
.add {
  margin-bottom: 10px !important;
}
.block {
  margin-bottom: 10px;
}
.el-pagination {
  margin-top: 10px;
}
</style>
