<template>
  <div>
    <el-card class="box-card">
      <el-button type="primary" @click="add()" icon="el-icon-circle-plus" class="add"></el-button>
      <!--  -->
      <el-table :data="tableData" border style="width: 100%" :header-cell-style="{ textAlign: 'center' }" :cell-style="{ textAlign: 'center' }">
        <el-table-column type="index" label="" width="50">
          <!-- <template slot-scope="scope">
            <span>{{ (currentPage-1)*pageSize+scope.$index+1 }}</span>
          </template> -->
        </el-table-column>
        <el-table-column prop="username" label="" width=""> </el-table-column>
        <el-table-column prop="id" label="id" width=""> </el-table-column>
        <el-table-column label="" width="">
          <template slot-scope="scope">
            <!-- {{ scope.row.id }} -->
            <div>
              <el-button type="primary" @click="showEditDialog(scope.row.id)" icon="el-icon-edit"></el-button>
              <el-button type="danger" @click="deleteAds(scope.row.id)" icon="el-icon-delete"></el-button>
            </div>
          </template>
        </el-table-column>
      </el-table>
    </el-card>
    <!--  -->
    <el-dialog title="" :visible.sync="editDialogVisible" width="50%" @close="editClose">
      <!-- rulesref -->
      <el-form ref="editFormRef" :model="editForm" label-width="80px" status-icon>
        <el-form-item label=":">
          <el-input v-model="editForm.username" prefix-icon="iconfont icon-user" placeholder="" clearable=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="editForm.oldPassword" prefix-icon="iconfont icon-3702mima" type="password" show-password placeholder=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="editForm.password" prefix-icon="iconfont icon-3702mima" type="password" show-password placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="cancel"> </el-button>
        <el-button type="primary" @click="subEdit()"> </el-button>
      </span>
    </el-dialog>
    <!--  -->
    <el-dialog title="" :visible.sync="addDialogVisible" width="50%" @close="addClose">
      <!-- rulesref -->
      <el-form ref="addFormRef" :model="addForm" label-width="80px" status-icon>
        <el-form-item label=":">
          <el-input v-model="addForm.username" prefix-icon="iconfont icon-user" placeholder="" clearable=""></el-input>
        </el-form-item>
        <el-form-item label=":">
          <el-input v-model="addForm.password" prefix-icon="iconfont icon-3702mima" type="password" show-password placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <span slot="footer" class="dialog-footer">
        <el-button @click="canceladd"> </el-button>
        <el-button type="primary" @click="addEdit()"> </el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import { getmain, editAccount, deleteAd, addAd } from '../../api/api'
export default {
  data() {
    return {
      tableData: [], //
      editDialogVisible: false,
      addDialogVisible: false,
      editForm: { username: '', oldPassword: '', password: '' }, //
      addForm: { username: '', password: '' } //
    }
  },
  created() {
    this.ready()
  },
  methods: {
    //
    ready() {
      getmain().then(res => {
        // console.log(res);
        if (res.code == 200) {
          this.tableData = res.data
        }
      })
    },
    //
    showEditDialog() {
      this.editDialogVisible = true
    },
    editClose() {
      this.editForm.username = ''
      this.editForm.oldPassword = ''
      this.editForm.password = ''
    },
    cancel() {
      this.editForm.username = ''
      this.editForm.oldPassword = ''
      this.editForm.password = ''
      this.editDialogVisible = false
    },
    //
    subEdit() {
      const data = {
        username: this.editForm.username,
        oldPassword: this.editForm.oldPassword,
        password: this.editForm.password
      }
      editAccount(data).then(res => {
        // console.log(res);
        if (res.code == 200) {
          this.$message.success('')
          this.editDialogVisible = false
          this.ready()
        } else {
          this.$message.error('')
        }
      })
    },
    //
    async deleteAds(id) {
      // console.log(id);
      const res = await this.$confirm(', ?', '', {
        confirmButtonText: '',
        cancelButtonText: '',
        type: 'warning'
      }).catch(err => {
        return err
      })
      //confirm
      //cancel
      console.log(res)
      if (res !== 'confirm') {
        return this.$message.info('~')
      } else {
        // console.log(id);
        const data = {
          id: id
        }
        //data 
        deleteAd(data).then(res => {
          // console.log('>>>'+res);
          if (res.code == 200) {
            this.$message.success('')
            this.ready()
          } else {
            return this.$message.error('')
          }
        })
      }
    },
    add() {
      this.addDialogVisible = true
    },
    //
    canceladd() {
      this.addForm.username = ''
      this.addForm.password = ''
      this.addDialogVisible = false
    },
    addClose() {
      this.addForm.username = ''
      this.addForm.password = ''
    },
    //
    addEdit() {
      const data = {
        username: this.addForm.username,
        password: this.addForm.password
      }
      addAd(data).then(res => {
        if (res.code == 200) {
          this.$message.success('')
          this.addDialogVisible = false
          this.ready()
        } else {
          this.$message.error('')
        }
      })
    }
  }
}
</script>

<style lang="less" scoped>
.add {
  margin-bottom: 10px;
}
</style>
